export default {
	data() {
		return {
			item: {},
			merchantID:1
		};
	},
	methods: {
		loadData(merchantID) {
			this.merchantID = merchantID
			console.log("loadData()")
			let url = '/api/merchant/Intro';
			let postData = { id: this.id };
			this.$H.post(url, postData).then(res => {
				console.log(res);
				this.item = res.data;
			});
		}
	}
};
